import WucTab from '@/components/wuc-tab/wuc-tab.vue';
import uniIcons from "@/components/uni-icons/uni-icons.vue"

export default {
	data() {
		return {
			username: '',
			password: '',
			code:'',
			repeatPassword:'',
			tabList: [{
					name: '老师',
					icon: 'cuIcon-comment'
				},
				{
					name: '学生',
					icon: 'cuIcon-dianhua'
				},
				{
					name: '家长',
					icon: 'cuIcon-wifi'
				}
			],
			userType: 0,
		};
	},
	components: {
		WucTab,
		uniIcons,
	},
	computed: {},
	onLoad() {
	},
	methods: {
		tabChange(index) {
			this.userType = index;
		},
		bindUser() {
			const {
				username,
				password,
				repeatPassword,
				userType,
				code,
			} = this
			
			if(repeatPassword !== password){
				uni.showModal({
					content: '两次密码不一致',
					showCancel: false
				})
				return
			}
			if (username.length < 6 || password.length < 6) {
				uni.showModal({
					content: '用户名密码长度均不能小于6',
					showCancel: false
				})
				return
			}
			uni.showLoading({
				title: '注册中...'
			})
			
			this.getCode().then((code) => {
				this.code = code;
			}).then((res) => {
				uniCloud.callFunction({
					name: 'wexinSingUp',
					data: {
						username,
						password,
						code:this.code,
						userType,
					},
				}).then((res) => {
					uni.hideLoading()
					console.log(res)
					if (res.result.status !== 0) {
						return Promise.reject(new Error(res.result.msg))
					}
					uni.setStorageSync('token', res.result.token)
					uni.showModal({
						content: '注册成功',
						showCancel: false
					})
				}).catch((err) => {
					uni.hideLoading()
					uni.showModal({
						content: '注册失败，' + err.message,
						showCancel: false
					})
				})
			})
		},
		getCode() {
			return new Promise((resolve, reject) => {
				uni.login({
					provider: 'weixin',
					success(e) {
						if (e.code) {
							resolve(e.code)
						} else {
							reject(new Error('微信登录失败'))
						}
					},
					fail(e) {
						reject(new Error('微信登录失败'))
					}
				})
			})
		},
	},
	onReady() {}
};
